<template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">

        <el-form-item label="" prop="regionone">
            <el-select v-model="ruleForm.regionone" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
                <el-option label="" value="0.8"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regiontwo">
            <el-select v-model="ruleForm.regiontwo" placeholder="">
                <el-option label="64G" value="1"></el-option>
                <el-option label="256G" value="0.9"></el-option>
                <el-option label="512G" value="0.8"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="regionthree">
            <el-select v-model="ruleForm.regionthree" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="0.9"></el-option>
